import Vue from "vue";

import Vuex from 'vuex'

Vue.use(Vuex)

const actions = {
    change(context, value) {
        context.commit('CHANGE', value)
    }
}

const mutations = {
    CHANGE(state, value) {
        state.scroH = value;
    },
    CHAGESONGINDEX(state, value) {
        state.isPlayIndex = value
    },
    CHANGEIMGURL(state, value) {
        state.imgSrc = value;
    },
    CHANGEIDARRAY(state, value) {
        state.idArray = value;
    },
    CHANGEURL(state, value) {
        state.url = value;
    },
    CHANGEID(state, value) {
        state.id = value
    }
}

const state = {
    num: 0,
    scroH: 0,
    isPlayIndex: -1,
    imgSrc: require("@/assets/background/img-insert/默认歌单.png"),
    idArray: [],
    url: '',
    id: 0,
}

const getters = {
    theScroH() {
        return state.scroH / 20 - 350;
    }
}

export default new Vuex.Store({
    actions,
    mutations,
    state,
    getters,
})