//该文件用于创造路由器
import VueRouter from "vue-router";

//引入组件
// import TheLogin from '../components/pages/TheLogin.vue'
// import TheIndex from '../components/TheIndex.vue'
// import TheGraphic from '../components/pages/TheGraphic.vue'


//创建并暴露一个路由器
const router = new VueRouter({
    routes: [
        {
            path: '/',
            redirect: '/TheIndex'
        },
        {
            name: 'MyMusic',
            path: '/MyMusic',
            redirect: '/MyMusic/MusicPlayer',
            component: () => import('../components/MusicComponent/MyMusic.vue'),
            meta: {
                title: '音乐播放器'
            },
            children: [
                {
                    name: 'MusicPlayer',
                    path: 'MusicPlayer',
                    component: () => import('../components/MusicComponent/MusicPlayer.vue'),
                    meta: {
                        title: '音乐播放'
                    }
                }
            ]
        },
        {
            name: 'TheLogin',
            path: '/TheLogin',
            // component: TheLogin,
            component: () => import('../components/pages/TheLogin.vue'),
            meta: {
                title: '登陆界面'
            }
        },
        {
            name: 'TheGraphic',
            path: '/TheGraphic',
            // component: TheGraphic,
            component: () => import('../components/pages/TheGraphic.vue'),
            meta: {
                title: '画廊'
            }
        },
        {
            name: 'TheIndex',
            path: '/TheIndex',
            // component: TheIndex,
            component: () => import('../components/TheIndex.vue'),
            meta: {
                title: '首页'
            }
        },
        {
            name: 'MyBlog',
            path: '/MyBlog',
            redirect: '/MyBlog/blogIntro',
            component: () => import("../components/pages/MyBlog.vue"),
            meta: {
                title: '我的博客'
            },
            children: [
                {
                    name: 'blogIntro',
                    path: 'blogIntro',
                    component: () => import('../components/blog/blogIntro.vue'),
                    meta: {
                        title: '我的博客'
                    },
                },
            ],
        },
    ]
})

//后置路由守卫
router.afterEach((to, from) => {
    document.title = to.meta.title
})


export default router