<template>
  <div id="app">
    <!-- <TheIndex></TheIndex> -->
    <!-- 导航栏 -->
    <TheNav></TheNav>
  </div>
</template>

<script>
// import TheIndex from "./components/TheIndex";
import TheNav from "./components/content/TheNav";
export default {
  name: "App",
  components: {
    // TheIndex,
    TheNav,
  },
  created() {
    document.body.removeChild(document.getElementById("loading")); // 加载页面完后移除加载动画
  },
  mounted() {
    console.log("屏幕宽度为:" + window.screen.width);

    // 禁用页面放大缩小
    document.addEventListener(
      "mousewheel",
      function (e) {
        e = e || window.event;
        if ((e.wheelDelta && event.ctrlKey) || e.detail) {
          event.preventDefault();
        }
      },
      {
        capture: false,
        passive: false,
      }
    );
  },
};
</script>

